body {
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    overflow: hidden;
}

#first {
    position: absolute;
}

nav {
    position: relative;
    color: white;
    z-index: 2;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0px;
    font-weight: bold;
}

nav a {
    text-decoration: none;
    color: white;
}

nav a:hover {
    color: #eee;
}

nav ul {
    display: flex;
    justify-content: space-between;
    gap: 4rem;
    list-style: none;
}

nav ul li:hover {
    color: #eee;
    cursor: pointer;
}

h1 {
    position: relative;
    z-index: 2;
    color: white;
    font-weight: bold;
    position: absolute;
    left: 50%;
    top: 85%;
    transform: translate(-50%, -50%);
}